.main-section {
    width: 85%;
    margin-left: auto;
    margin-right: auto;
    min-height: 600px;
}
.main {
    display: flex;
    flex-direction: column;
    background: url('../images/Muir-Wood.jpg');
    background-attachment: fixed;
    background-size: cover;
    padding-bottom: 25px;
    padding-top: 75px;
    animation: fadeIn .75s;
}
@keyframes fadeIn {
    0% {
        opacity: 0.1
    }
    100% {
        opacity: 1
    }
}
.gm-svpc {
    visibility: hidden;
}
.gm-fullscreen-control {
    visibility: hidden;
}
.gm-style-mtc{
    visibility: hidden;
}



